<template>
  <div class="careerModelBox">
    <div class="careerModelList">
      <div class="careerModel-title">
        <h1>{{careerGuidance.guidanceTitle}}</h1>
      </div>
      <div class="careerModel-tip">
        <p>{{careerGuidance.counselorNo}}</p>
        <p>最后发布于 {{careerGuidance.releaseDate}}</p>
        <p>阅读数 {{careerGuidance.viewNum}}</p>
      </div>
      <div class="careerModel-content" v-html="careerGuidance.content"></div>
    </div>
  </div>
</template>
<script>
import {
  interGuidanceInfo,
  updateGuidanceInfo
} from "../../../../../utils/employment.url";
export default {
  data() {
    return {
      careerGuidance: {}
    };
  },
  methods: {
    //用户信息获取就业指导列表信息
    getGuidanceInfo(id) {
      interGuidanceInfo({ id: id ? id : "" }, "get")
        .then(res => {
          if (res.status == 200) {
            this.careerGuidance = res.data;
            this.careerGuidance.releaseDate = new Date(
              this.careerGuidance.releaseDate
            )
              .toLocaleDateString()
              .replace(/\//g, "-");
            this.updateGuidanceInfo(this.careerGuidance);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //更新就业指导阅读量
    updateGuidanceInfo(params) {
      updateGuidanceInfo(params, "put").then(res => {
        if (res.status == 200) {
        }
      });
    }
  },
  created() {
    this.getGuidanceInfo(this.$route.params.id);
  }
};
</script>
<style lang="scss" scoped>
.careerModelBox {
  width: 1200px;
  margin: 10px auto;
  .careerModelList {
    padding: 25px;
    :nth-of-type(n) {
      margin: 10px auto;
    }
    .careerModel-title {
      font-size: 28px;
      word-wrap: break-word;
      color: #000;
      font-weight: bold;
    }
    .careerModel-tip {
      p {
        display: inline-block;
        color: #858585;
        line-height: 26px;
        margin-right: 50px;
        font-family: "Microsoft YaHei", "SF Pro Display", Roboto, Noto, Arial,
          "PingFang SC", sans-serif;
      }
    }
    .careerModel-content {
      overflow: hidden;
      line-height: 25px;
    }
  }
}
</style>